#loader {
  width: 150px;
  height: 150px;
  border: 3px solid #0000;
  border-top-color: #9370db;
  border-radius: 50%;
  margin: -75px 0 0 -75px;
  animation: 2s linear infinite spin;
  display: block;
  position: relative;
  top: 50%;
  left: 50%;
}

#loader:before {
  content: "";
  border: 3px solid #0000;
  border-top-color: #ba55d3;
  border-radius: 50%;
  animation: 3s linear infinite spin;
  position: absolute;
  inset: 5px;
}

#loader:after {
  content: "";
  border: 3px solid #0000;
  border-top-color: #f0f;
  border-radius: 50%;
  animation: 1.5s linear infinite spin;
  position: absolute;
  inset: 15px;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

